import React from 'react';
import { Container, Form, FormControl } from 'react-bootstrap';
import { withRouter } from 'react-router-dom';

const inputWidth = {
    width: '25em'
}

class MyLogin extends React.Component {
    constructor() {
        super()
        this.state = {}
        this.login = (e) => {
            e.preventDefault()
            fetch('http://localhost:7500/admin/login', {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(this.state)
            })
                .then(res => res.json())
                .then(results => {
                    console.log(results);
                   
                    let adminMessage = document.getElementById('adminMessage');
                    adminMessage.innerText = `${results.message}`
                    setTimeout(function () {
                        adminMessage.innerText = ""
                    }, 3000)
                    if (results.authenticated) {
                        this.props.admin(results)
                        const {
                            history
                        } = this.props
                        history.push(`/myContacts`)
                    }
                })
        }
        this.userInput = (e) => {
            this.setState({
                [e.target.id]: e.target.value
            })
        }

    }
    render() {
        return (
            <Container className='text-center bg text-white' id='myLogin'>
            <h1 className='mt-5 mb-3' style={{ color: '#FFE4C4' }}>Login</h1>
            <p id='adminMessage'></p>
                <Form onSubmit={(event) => {
                    this.login(event)
                }}>
                    <FormControl type='email' className='mx-auto d-block mb-3' style={inputWidth} placeholder='Email' id='logEmail' onChange={this.userInput} required />
                    <FormControl type='password' className='mx-auto d-block mb-3' style={inputWidth} placeholder='Password' id='logPwd' onChange={this.userInput} required />
                    <button style={{backgroundColor: 'rgb(179, 167, 142)'}} className='btn text-white mb-5' type="submit" value="Send Message">Enter</button>
                </Form>
            </Container>

        )
    }
}
export default withRouter(MyLogin)