import React from 'react';
import { Container, Form, Row, InputGroup, FormControl } from 'react-bootstrap';


class Contact extends React.Component {
    constructor(){
        super()
        this.state = {}
        this.contact = (e) => {
            e.preventDefault()
            fetch('http://localhost:7500/contact/add', {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(this.state)
            })
                .then(res => res.json())
                .then(results => {
                   console.log("this is the results", results);
                   
                })
        }
        this.userInput = (e) => {
            this.setState({
                [e.target.id]: e.target.value
            })
        }
    }
    render() {
        return (
            <Container  className="text-center bg text-white" id="contact">
                <h1 className='mt-5 mb-3' style={{ color: '#FFE4C4' }}>Contact</h1>
                <Form action="#" method="POST" onSubmit={(event) => {
                    this.contact(event)
                }}>
                    <InputGroup className='mb-3'>
                        <FormControl placeholder='First Name' id='fName' onChange={this.userInput} required />
                        <FormControl placeholder='Last Name' id='lName' onChange={this.userInput} className='ml-2' required />
                    </InputGroup>
                    <InputGroup className='mb-3'>
                        <FormControl type='email' id='email' onChange={this.userInput} placeholder='Email' required />
                        <FormControl type='telephone' id='phone' onChange={this.userInput} placeholder='Phone Number' className='ml-2' />
                    </InputGroup>
                    <InputGroup className='mb-3'>
                        <FormControl as='textarea' id='message' onChange={this.userInput} placeholder='Leave a message...' required />
                    </InputGroup>
                    <Row id="contactBtns" className="row justify-content-center" >
                        <button style={{backgroundColor: 'rgb(179, 167, 142)'}} className='mr-3 btn text-white' type="submit" value="Send Message">Send Message</button>
                        <button type="reset" style={{backgroundColor: 'rgb(179, 167, 142)'}} className="btn text-white" value="Reset">Reset</button>
                    </Row>
                </Form >
                <Container className='mt-5 mb-3'>
                    <h3 style={{ color: '#FFE4C4' }}>Stay in touch with me:</h3>
                    <i className="fab fa-facebook-square fa-2x mr-3"></i>
                    <i className="fab fa-github-square fa-2x mr-3"></i>
                    <i className="fab fa-gitlab fa-2x mr-3"></i>
                    <i className="fab fa-linkedin fa-2x mr-3"></i>
                </Container>
            </Container >
        )
    }
}

export default Contact;