import React from 'react';
import { Container, Image } from 'react-bootstrap';


export default (props) => {
    return (
        <Container>
            <a className="mt-3" href={props.href}>
                <Image src={props.src} alt={props.alt} className='img-fluid' style={{border: '5px outset rgb(179, 167, 142)'}} />
            </a>
            <p className="mt-3">{props.desc}</p>
        </Container>
    )
}


