import React from 'react';
import { Nav } from 'react-bootstrap';
import {Link} from 'react-router-dom';

const navbarBrand = {
    fontSize: '1.5em',
    color: 'bisque'
}
const navLink = {
    color: 'bisque'
}
class Navigation extends React.Component {
    render() {
        return (
            <Nav className="navbar navbar-expand-lg sticky-top">
                <Nav.Item style={navbarBrand} to="/info">Molly Rowe </Nav.Item>
                <Nav className="collapse navbar-collapse justify-content-end"  id="navbarNav">
                    <Link style={navLink} className="nav-link" to='/info' data-value="info">Home <span className="sr-only">(current)</span></Link>
                    <Link style={navLink} className="nav-link" to='/work' data-value="work">Work</Link>
                    <Link style={navLink} className="nav-link" to="/about" data-value="about">About</Link>
                    <Link style={navLink} className="nav-link" to="/contact" data-value="contact">Contact</Link>
                </Nav>
            </Nav>
        )
    }
}
export default Navigation;