import React from 'react';
import {Container, Row} from 'react-bootstrap';

class MyContacts extends React.Component{
    constructor(props){
        super(props)
        this.state = {}
        
        
    }
    render(){
        return(
            <Container className='bg text-center text-white mt-5'>
               <Row>
                   {this.props.contacts.results ? this.props.contacts.results.map((contact)=>{
                       return <div className='mt-3' key='contact'><h1>{contact.fName} {contact.lName} </h1><h3>{contact.email}</h3><h5>{contact.phone}</h5><p>{contact.message}</p></div>
                   }) : null}
                   
               </Row>
            </Container>

        )
    }
}
export default MyContacts